
.compontents-layout{
	position: relative;
}
.left-bar{
	position: absolute;
	width: 180px;
	text-align: center;
	border-right: 1px #ccc solid;
}
.main-content{
	padding-left: 200px;
	margin-top: 0;
}
.left-bar li{
	padding: 10px 0;
	font-size: 14px;
	position: relative;
}
.left-bar li a{
	color: #666;
		font-size: 14px;
}
.left-bar li.router-link-active a{
	color: #2db7f5;
	font-weight: 700;
}
.left-bar li:hover a{
	color: #2db7f5;
}
.left-bar .menu li.router-link-active::after{
	content: '';
	border-left: 3px solid #2db7f5;
	position: absolute;
	right: -2px;
	top: 0;
	height: 39px;
}
.memu-item.router-link-active{
	color: #2db7f5;
}
.memu-item{
	color: #333;
}
h4{
	padding: 20px 0;
}



